<template lang="html">
  <v-container fluid fill-height>
    <v-row align-center justify-center>
      <v-col cols='12' sm='8' md='6' >
        <v-card elevation="2" v-if="loading">
          <v-card-text class="pa-5">
            <v-progress-circular indeterminate />
          </v-card-text>
        </v-card>

        <v-card elevation="2" v-else-if="error.state">
          <v-card-text class="pa-2">
            <h4>
              {{error.message}}
            </h4>
          </v-card-text>
          <v-card-actions>
            <v-btn @click="getData" color="error">
              Retry
            </v-btn>
          </v-card-actions>
        </v-card>

        <v-card elevation="2" v-else>
          <v-card-title>
            <v-toolbar color="transparent" flat>
              <v-toolbar-title>
                $10
              </v-toolbar-title>
              <v-spacer />
              <h3>BTC {{amount}}</h3>
            </v-toolbar>
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols='12' px-3>
                <v-img :src="payment.qrcode_url">
                </v-img>
              </v-col>
              <v-col cols='12' mt-3>
                <h3>Address: {{block.address}}</h3>
              </v-col>
            </v-row>
          </v-card-text>
          <v-card-actions>
            <v-btn @click='progress' color="primary" dark> Continue
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>

</template>

<script>
import img from "../../assets/bg0.jpg"
export default {
  data(){
    return{
      payment:{
        amount:0,amount_paid:0,date_issued:4,block_transaction:[0,2,1]
      },
      loading:false,
      error:{
        state:false,message:''
      }

    }
  },
  computed:{
    block(){
      return this.payment.block_transaction[this.payment.block_transaction.length-1]
    },
    amount(){
      return (this.payment.amount-this.payment.amount_paid).toFixed(8)
    }
  },
  methods:{
    progress(){
      try {
        this.$store.dispatch('updateAccount')
        this.$store.dispatch('profileUpdate')
        this.$router.replace({path:'/'})
        
      } catch (error) {
        console.log(error);
        
      }
    },
    async getData(){
      this.loading = true
      this.error = {state:false,message:''}
      try {
        let response = await this.axios.get('system/payment')
        this.payment=response.data
      } catch (error) {        
        this.processResponseError(error)
      }
      this.loading = false
    },

    processResponseError(response){      
      this.error = {state:true,message:""}
      if(response.response){
        if(response.response.status>=500)
        this.error.message = 'Error in processing, please try again later'
      }else{
        this.error.message = "Failed to connect to server, Please check your internet connection"
      }

    }
  },
  mounted(){
      this.getData()
  }
}
</script>

